@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;300;500;700&display=swap);
* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}
@media only screen and (max-width: 75em) {
  html {
    font-size: 56.25%;
  }
}
@media only screen and (max-width: 56.75em) {
  html {
    font-size: 50%;
  }
}
@media only screen and (min-width: 112.5em) {
  html {
    font-size: 75%;
  }
}

body {
  background-color: #222;
  margin: 0 auto;
}

a {
  text-decoration: none;
  outline-offset: -0.1rem;
}
a:focus {
  outline: 1px dotted #212121;
  outline: 5px auto -webkit-focus-ring-color;
}
a:focus:-moz-focusring {
  outline: 1px solid #f7f7f7;
  outline: 5px auto -webkit-focus-ring-color;
}

.header {
  display: block;
}
.header__title {
  text-align: center;
}

.clear:after {
  content: '';
  display: block;
  clear: both;
}

body {
  font-weight: 400;
  line-height: 1.6;
  font-family: 'Raleway', sans-serif;
  font-size: 1.7rem;
}

@keyframes blink {
  from,
  to {
    color: transparent;
  }
  50% {
    color: #f7f7f7;
  }
}

@keyframes appear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes slide {
  0% {
    transform: translateX(50rem);
  }
  100% {
    transform: translateX(50rem);
  }
}

@keyframes flip {
  0% {
    transform: rotateY(-180deg);
  }
  100% {
    transform: rotateY(0deg);
  }
}

@keyframes slide-in {
  0% {
    opacity: 0;
    transform: translateY(5rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.u-color-primary {
  color: #4a849f;
  cursor: pointer;
}

.u-color-red {
  color: #ff9c67;
  font-weight: 700;
}

.u-center {
  text-align: center !important;
}

.u-indent {
  margin-left: 2rem;
}

