.faccia {
  position: absolute;
  display: block;
  height: 10rem;
  width: 10rem;
}

.faccia span {
  position: absolute;
  color: white;
  top: 4rem;
  display: block;
  text-align: center;
  width: 100%;
  font-family: 'Poppins', sans-serif;
}

.faccia1 {
  background: #4a849f;
  transform: translateZ(5rem);
  animation: explode1 10s infinite ease-in-out;
}

.faccia2 {
  background: #c8326f;
  transform: rotateY(90deg) translateZ(5rem);
  animation: explode2 10s infinite ease-in-out;
}

.faccia3 {
  background: #34b5e0;
  transform: rotateY(180deg) translateZ(5rem);
  animation: explode3 10s infinite ease-in-out;
}

.faccia4 {
  background: #55cef2;
  transform: rotateY(-90deg) translateZ(5rem);
  animation: explode4 10s infinite ease-in-out;
}

.faccia5 {
  background: #b62e65;
  transform: rotateX(90deg) translateZ(5rem);
  animation: explode5 10s infinite ease-in-out;
}

.faccia6 {
  background: #2ea1c7;
  transform: rotateX(-90deg) translateZ(5rem);
  animation: explode6 10s infinite ease-in-out;
}

/* ==================== EXPLODE 1 =================== */

@keyframes explode1 {
  0% {
    transform: translateZ(5rem);
  }
  25% {
    transform: translateZ(10rem);
  }
  75% {
    transform: translateZ(10rem);
  }
  100% {
    transform: translateZ(5rem);
  }
}

/* ==================== EXPLODE 2 =================== */

@keyframes explode2 {
  0% {
    transform: rotateY(90deg) translateZ(5rem);
  }
  25% {
    transform: rotateY(90deg) translateZ(10rem);
  }
  75% {
    transform: rotateY(90deg) translateZ(10rem);
  }
  100% {
    transform: rotateY(90deg) translateZ(5rem);
  }
}

/* ==================== EXPLODE 3 =================== */

@keyframes explode3 {
  0% {
    transform: rotateY(180deg) translateZ(5rem);
  }
  25% {
    transform: rotateY(180deg) translateZ(10rem);
  }
  75% {
    transform: rotateY(180deg) translateZ(10rem);
  }
  100% {
    transform: rotateY(180deg) translateZ(5rem);
  }
}

/* ==================== EXPLODE 4 =================== */

@keyframes explode4 {
  0% {
    transform: rotateY(-90deg) translateZ(5rem);
  }
  25% {
    transform: rotateY(-90deg) translateZ(10rem);
  }
  75% {
    transform: rotateY(-90deg) translateZ(10rem);
  }
  100% {
    transform: rotateY(-90deg) translateZ(5rem);
  }
}

/* ==================== EXPLODE 5 =================== */

@keyframes explode5 {
  0% {
    transform: rotateX(90deg) translateZ(5rem);
  }
  25% {
    transform: rotateX(90deg) translateZ(10rem);
  }
  75% {
    transform: rotateX(90deg) translateZ(10rem);
  }
  100% {
    transform: rotateX(90deg) translateZ(5rem);
  }
}

/* ==================== EXPLODE 6 =================== */

@keyframes explode6 {
  0% {
    transform: rotateX(-90deg) translateZ(5rem);
  }
  25% {
    transform: rotateX(-90deg) translateZ(10rem);
  }
  75% {
    transform: rotateX(-90deg) translateZ(10rem);
  }
  100% {
    transform: rotateX(-90deg) translateZ(5rem);
  }
}

/* ================== ROTATE ================== */

@keyframes rotate {
  0% {
    transform: rotateX(0deg) rotateY(0deg);
  }
  5% {
